import React, { useEffect, useRef } from 'react';
import { Arrow, Blur, Container, Content, Img } from './style';
import { Carousel } from 'antd';
import img1 from '../../assets/img/house1.png';
import img2 from '../../assets/img/house2.png';
import img3 from '../../assets/img/house3.png';
import img4 from '../../assets/img/house4.png';

function GenCarousel() {
    const slider = useRef(null);

    const onMove = ({ target: { dataset: { name } } }) => {
        if (!slider.current) return;
        if (name === 'left') slider.current.prev();
        if (name === 'right') slider.current.next();
    };

    useEffect(() => {
        const interval = setInterval(() => {
            if (slider.current) {
                slider.current.next(); 
            }
        }, 5000);

        return () => clearInterval(interval);
    }, []);

    return (
        <Container>
            <Carousel ref={slider}>
                <Img src={img1} />
                <Img src={img2} />
                <Img src={img3} />
                <Img src={img4} />
            </Carousel>
            <Blur />
            <Content>
                <Content.Title>Skyper Pool Partment</Content.Title>
                <Content.Desc>112 Glenwood Ave Hyde Park, Boston, MA</Content.Desc>
                <Content.Price>$5,250/mo</Content.Price>
            </Content>
            <Arrow data-name="left" onClick={onMove} $left={true} />
            <Arrow data-name="right" onClick={onMove} />
        </Container>
    );
}

export default GenCarousel;
