import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import CategoryCard from '../CategoryCard';
import { Navigation, Pagination } from 'swiper/modules';
import { Arrow, Container } from './style';
import { useNavigate } from 'react-router-dom';

function Category() {
    const [data, setData] = useState([]);
    const navigate = useNavigate()

    useEffect(() => {
        fetch(`https://housing-backend-production.up.railway.app/api/categories/list`)
            .then(res => res.json())
            .then(res => {
                setData(res || []);
            });
    }, []);

    useEffect(() => {
        const swiperInstance = document.querySelector('.swiper');
        if (swiperInstance && swiperInstance.swiper) {
            swiperInstance.swiper.params.navigation.prevEl = document.querySelector('.category-prev-button');
            swiperInstance.swiper.params.navigation.nextEl = document.querySelector('.category-next-button');
            swiperInstance.swiper.navigation.init();
            swiperInstance.swiper.navigation.update();
        }
    }, [data]);


    return (
        <Container>
            <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
                <h1 className="title">Category</h1>
                <p className="info" style={{ fontSize: "16px", marginTop:"8px", marginBottom:"32px" }}>Nulla quis curabitur velit volutpat auctor bibendum consectetur sit.</p>
            </div>
            <Swiper
                modules={[Navigation, Pagination]}
                navigation={{
                    prevEl: '.category-prev-button',
                    nextEl: '.category-next-button',
                }}
                slidesPerView={4}
                spaceBetween={20}
                loop={data.length > 4}
                pagination={{ clickable: true }}
            >
                {data.map((value, index) => (
                    <SwiperSlide key={index}>
                        <CategoryCard onClick={() => navigate(`/properties?category=${value?.name}`)} data={value} />
                    </SwiperSlide>
                ))}
            </Swiper>
            <Arrow className="category-prev-button" $left />
            <Arrow className="category-next-button" />
        </Container>
    );
}

export default Category;
