import styled from "styled-components";
import { ReactComponent as arrow } from '../../assets/icons/arrow.svg'

const Container = styled.div`
    position: relative;
    width: 100%;
    max-width: 1440px;    
    padding: 96px 130px;
    margin: 0 auto;
`

const Img = styled.img`
    width: 10%;
    height: auto;
    max-height: 571px;
`

const Arrow = styled(arrow)`
    position: absolute;
    z-index: 999;
    padding: 15px;
    top: 50%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow: 0px 10px 30px 5px #0D263B1A;
    background-color: white;
    left: ${({ $left }) => ($left && '-25px')};
    right: ${({ $left }) => (!$left && '-25px')};
    transform: rotate(${({ $left }) => (!$left && '180deg')});
    cursor: pointer;
    path {
    fill: black;
    }
`

const Content = styled.div`
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
`

Content.Title = styled.h1`
    font-family: Montserrat;
    font-weight: 700;
    font-size: 44px;
    line-height: 48px;
    letter-spacing: -2%;
`


export { Container, Arrow, Img, Content }