import styled from "styled-components";
import { ReactComponent as Email } from "../../assets/icons/cardEmail.svg";
import { ReactComponent as House } from "../../assets/icons/cardHouse.svg";
import { ReactComponent as Calculator } from "../../assets/icons/cardCalculator.svg";
import { ReactComponent as Maps } from "../../assets/icons/cardMaps.svg";


const Container = styled.div`
    position: relative;
    width: 100%;
    height: 434px;
    padding-top: 48px;
    background-color: #F5F7FC;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const Content = styled.div`
    width: 235px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    text-align: center;
`

const Wrapper = styled.div`
    max-width: 1440px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    padding: 0 130px;
`

const Icons = styled.div``
Icons.Email = styled(Email)`
    margin-bottom: 24px;
`
Icons.House = styled(House)`
    margin-bottom:24px;
`
Icons.Calculator = styled(Calculator)`
    margin-bottom:24px;
`
Icons.Maps = styled(Maps)`
    margin-bottom:24px;
`



export { Container, Content, Wrapper, Icons }