import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import HouseCard from '../HouseCard';
import { Navigation, Pagination } from 'swiper/modules';
import { Arrow, Container, Content } from './style';
import { useNavigate } from 'react-router-dom';

function Recommended() {
    const [data, setData] = useState([]);
    const navigate = useNavigate()

    useEffect(() => {
        fetch(`https://housing-backend-production.up.railway.app/api/houses`)
          .then(res => res.json())
          .then(res => {
            setData(res || []);
          });
      }, []);

    useEffect(() => {
        const swiperInstance = document.querySelector('.swiper');
        if (swiperInstance) {
            swiperInstance.swiper.params.navigation.prevEl = '.custom-prev';
            swiperInstance.swiper.params.navigation.nextEl = '.custom-next';
            swiperInstance.swiper.navigation.init();
            swiperInstance.swiper.navigation.update();
        }
    }, [data]);

    return (
        <Container>
            <Content>
                <h1 className="title">Recommended</h1>
                <p className="info" style={{fontSize:"16px"}}>Nulla quis curabitur velit volutpat auctor bibendum consectetur sit.</p>
            </Content>
            <Swiper
                style={{height: "500px"}}
                modules={[Navigation, Pagination]}
                slidesPerView={3}
                spaceBetween={20}
                loop={data.length > 3}
                pagination={{ clickable: true }}
            >
                {data.map((value, index) => (
                    <SwiperSlide key={index}>
                        <HouseCard onClick={() => navigate(`/properties/${value._id}`)} data={value} />
                    </SwiperSlide>
                ))}
            </Swiper>
            <Arrow className="custom-prev" $left />
            <Arrow className="custom-next" />
        </Container>
    );
}

export default Recommended;
