import styled from "styled-components";
import { ReactComponent as arrow } from '../../assets/icons/arrow.svg'

const Container = styled.div`
    position: relative;
    height: fit-content;
    overflow: hidden;
`

const Img = styled.img`
    width: 100%;
    min-height: auto;
    height: 571px;
    max-height: 571px;
`

const Arrow = styled(arrow)`
    position: absolute;
    padding: 15px;
    top: 50%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.2);
    left: ${({ $left }) => ($left && '20px')};
    right: ${({ $left }) => (!$left && '20px')};
    transform: rotate(${({ $left }) => (!$left && '180deg')});
    cursor: pointer;
    path {
    fill: white;
    }
    &:hover {
        background-color: rgba(255, 255, 255, 0.3);
    }
`

const Blur = styled.div`
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
`

const Content = styled.div`
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
`

Content.Title = styled.h1`
    font-family: Montserrat;
    font-weight: 700;
    font-size: 44px;
    line-height: 48px;
    letter-spacing: -2%;
`

Content.Desc = styled.p`
    font-family: Montserrat;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
`

Content.Price = styled.p`
    font-family: Montserrat;
    font-weight: 600;
    font-size: 28px;
    line-height: 36px;
    letter-spacing: -2%;
`

export { Container, Arrow, Img, Blur, Content }